.p-notfound {
  z-index: 5;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100vh;
  color: #e8a163;
  text-align: center;
  padding-top: 1.875rem;
}
.p-notfound-icon {
  font-size: 13.75rem;
}
.p-notfound h1 {
  font-size: 4.0625rem;
  display: inline-block;
  padding-right: 0.75rem;
  -webkit-animation: type .5s alternate infinite;
          animation: type .5s alternate infinite;
  text-align: center;
  width: 100%;
  font-weight: 400;
}
